import React from "react";
import { useTranslation } from "react-i18next";

import NavBar from "../../../../components/NavBar/NavBar";
import article_list from "../../../../Data/ArticleList";
import ArticleCard from "../../../../components/ArticleCard/ArticleCard";
import QuoteSteps from "../../../QuoteSteps/QuoteSteps";

import "./ArticlesHome.css";

function ArticlesHome() {
  const { t } = useTranslation();
  return (
    <div className="articles-home">
      <NavBar />
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-7 center">
            <h3 className="header-text-2">List of Articles</h3>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-4 col-md-4 col-lg-4 mb-3">
            <ArticleCard render_data={article_list[0]} />
          </div>
          <div className="col-sm-4 col-md-4 col-lg-4 mb-3">
            <ArticleCard render_data={article_list[1]} />
          </div>
          <div className="col-sm-4 col-md-4 col-lg-4 mb-3">
            <ArticleCard render_data={article_list[2]} />
          </div>
        </div>
        <div className="row">
          <div className="col-sm-4 col-md-4 col-lg-4 mb-3">
            <ArticleCard render_data={article_list[3]} />
          </div>
          <div className="col-sm-4 col-md-4 col-lg-4 mb-3">
            <ArticleCard render_data={article_list[4]} />
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-12 col-md-12 col-lg-12 mt-5">
            <hr className="loan-apply-hr"></hr>
            <QuoteSteps stepOneSub={t("fhaLoan.section_10.txt_13")} />
            <hr className="loan-apply-hr"></hr>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ArticlesHome;
